.key{
  border: .4rem solid black;
  border-radius: .5rem;
  margin: .5rem;
  font-size: 1.5rem;
  padding: 1rem .2rem;
  transition: all .07s ease;
  width: 8.5rem;
  text-align: center;
  color: white;
  background: rgba(0,0,0,0.4);
  text-shadow: 0 0 .5rem black;
}

.playing {
  transform: scale(1.1);
  border-color: #ffc600;
  box-shadow: 0 0 1rem #ffc600;
}



.sound {
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: .1rem;
  color: #ffc600;
}

kbd {
  display: block;
  font-size: 2rem;
}